import {BasketActionType} from '../../../types/actions'
import T from '../../actions/actionTypes'
import {ProductForBasketInterface} from '../../../types/models/product-for-basket-interface'

export const initialState: ProductForBasketInterface[] = []

export default function basket(
    state: ProductForBasketInterface[] = initialState,
    action: BasketActionType
): ProductForBasketInterface[] {
    switch (action.type) {
        case T.ADD_PRODUCT_TO_BASKET: {
            console.log('basket.   ', action.product)
            let indexEqualProductConfiguration = 0
            const equalProductConfiguration = state.find((product, i) => {
                //       
                if (product.id === action.product.id) {
                    console.log('  ')
                    console.log(
                        '    ',
                        product.selectedProperties
                    )
                    console.log(
                        '   ',
                        action.product.selectedProperties
                    )
                    //    ,
                    //    , 
                    //    
                    let propertiesAreEqual = true
                    for (const propertyTypeId in product.selectedProperties) {
                        if (
                            product.selectedProperties[propertyTypeId] !==
                            action.product.selectedProperties[propertyTypeId]
                        ) {
                            propertiesAreEqual = false
                            break
                        }
                    }
                    indexEqualProductConfiguration = i
                    return propertiesAreEqual === true
                }
                return false
            })
            //    ,
            if (equalProductConfiguration !== undefined) {
                //      
                const filteredState = [...state]
                //    1
                equalProductConfiguration.amount += 1
                //   ,
                //   
                filteredState.splice(
                    indexEqualProductConfiguration,
                    1,
                    equalProductConfiguration
                )
                console.log(
                    'equalProductConfiguration',
                    equalProductConfiguration
                )
                return filteredState
            } else {
                return [...state, {...action.product, amount: 1}]
            }
        }
        case T.DELETE_PRODUCT_FROM_BASKET:
            return state.filter(
                (product, i) => i !== action.productConfigurationId
            )
        default:
            return state
    }
}
